// Reveal.tsx
import React from 'react';
import { useReveal } from './useReveal';
import './reveal.css';

interface RevealProps {
  children: React.ReactNode;
  direction?: 'left' | 'right' | 'up' | 'down' | 'scale' | 'none';
  delay?: number;
  threshold?: number;
  triggerOnce?: boolean;
  className?: string;
}

export const Reveal: React.FC<RevealProps> = ({
  children,
  direction = 'up',
  delay = 0,
  threshold = 0.1,
  triggerOnce = true,
  className = ''
}) => {
  const { ref, isRevealed } = useReveal({ threshold, triggerOnce });

  const directionClass = direction !== 'none' ? `reveal-${direction}` : '';
  const delayClass = delay > 0 ? `reveal-delay-${Math.min(Math.floor(delay / 100), 6)}` : '';

  return (
    <div
      ref={ref}
      className={`
        reveal-section 
        ${directionClass} 
        ${delayClass} 
        ${isRevealed ? 'revealed' : ''} 
        ${className}
      `}
      style={delay > 0 ? { transitionDelay: `${delay}ms` } : undefined}
    >
      {children}
    </div>
  );
};